.menubalk {
  width: 100%;
  height: auto;
  min-height: 25px;
  z-index: 997;
  vertical-align: middle;
  font-size: 75%;
  background-color: #572600;
}



.menubalk ul {
  padding: 2px 0;
  margin: 0;
  overflow: hidden;
}

.menubalk li {
  float: left;
  list-style-type: none;
  margin: 2px 12px 0 0;
  line-height: 17px;
}

.menubalk_content {
  padding: 0;
}

.menubalk a:link,
.menubalk a:visited,
.menubalk a:hover,
.menubalk a:active,
.menubalk a:focus {
  color: #FFF;
  text-decoration: none;
  font-size: 15px;
}

.menubalk a:hover,
.menubalk a:active,
.menubalk a:focus {
  color: #BDD732;
}

.item_sgv a {
  display: inline-block;
  height: 15px;
  width: 15px;
  text-indent: -99999px;
  background: url("https://www.scoutsengidsenvlaanderen.be/files/algemeen/menubalk/balk_sprites_sgv.png") no-repeat;
}

li.org a {
  /* 'font-weight: bold;' doesn't make it stand out. */
  font-weight: 800;
}

.item_facebook a,
.item_twitter a {
  text-indent: 0;
  margin: 0;
  width: 15px;
  height: auto;
  font-size: calc(15 * 6.25%);
  background: no-repeat 0 -15px;
}

img.custom_logo_image.linked {
  display: none;
}

.account {
  position: absolute;
  top: 0;
  right: 280px;
  color: #FFF;
}

.account li {
  margin-right: 6px;
}

.item_home a,
.item_help a,
.item_afmelden a {
  display: inline-block;
  position: relative;
  top: -4px;
  height: 20px;
  width: 20px;
  margin-top: 2px;
  text-indent: -99999px;
  background-image: url("https://www.scoutsengidsenvlaanderen.be/files/algemeen/menubalk/balk_sprite_account.png");
}

.item_home a {
  background-position: -20px -20px;
}

.item_help a {
  background-position: 0 -20px;
}

.item_afmelden a {
  background-position: -40px -20px;
}

.menu-icon {
  font-size: 15px;
}
.pull-right{
  float: right;
}

@media screen and (max-width: 1150px) {
  .menubalk_content {
    margin: 0 0 0 20px;
  }
  .account {
    right: 20px;
  }
}
